@import url(https://fonts.googleapis.com/css?family=Merriweather:300,400,700&display=swap&subset=cyrillic,cyrillic-ext,latin-ext);
html,
body,
#root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Merriweather', serif;
  font-size: 16px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f1f1f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.app {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  box-sizing: border-box;
}

.timeline {
  display: flex;
  align-items: center;
  scroll-behavior: smooth;
  overflow: hidden;
  touch-action: pan-x;
  position: relative;
  padding-top: 45px; }
  .timeline__content, .timeline__year, .timeline__month {
    display: flex;
    align-items: center;
    position: relative;
    flex-grow: 1;
    flex-shrink: 0; }
  .timeline__year, .timeline__month {
    margin-right: 15px; }
    .timeline__year:last-child, .timeline__month:last-child {
      margin-right: 0; }
  .timeline__year-header, .timeline__month-header {
    position: absolute;
    top: -45px;
    left: 0;
    border-radius: 15px;
    background-color: #fff;
    padding: 5px 10px;
    -webkit-transition: left .2s ease;
    transition: left .2s ease;
    will-change: 'left'; }

.day {
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  margin-right: 15px;
  cursor: pointer; }
  .day:last-child {
    margin-right: 0; }
  .day:hover, .day_active {
    background-color: #2d96fd; }
    .day:hover .day-header__date,
    .day:hover .day-header__day-of-week,
    .day:hover .day__balance, .day_active .day-header__date,
    .day_active .day-header__day-of-week,
    .day_active .day__balance {
      color: #fff; }

.day-header {
  display: flex;
  align-items: center; }

.day-header__date {
  font-weight: 700;
  color: #2d96fd;
  font-size: 30px;
  margin-right: auto; }

.day-header__day-of-week {
  color: #aaaaaa; }

.day-header__content {
  margin: 10px 0; }

.day__income {
  padding: 10px 0;
  text-align: center;
  color: rgba(0, 255, 0, 0.3);
  font-size: 18px;
  font-weight: 700; }

.day__consumption {
  padding: 10px 0;
  text-align: center;
  color: rgba(255, 0, 0, 0.5);
  font-size: 18px;
  font-weight: 700; }

.day__balance {
  font-weight: 700;
  font-size: 32px;
  color: #000; }

